import { Form, Table } from "@themesberg/react-bootstrap";
import React, { useEffect, useMemo, useRef } from "react";
import { Suspense } from "react";
import AutosizeInput from "react-input-autosize";
import { useSelector } from "react-redux";
import TextareaAutosize from "react-textarea-autosize";
import styled from "styled-components";
import {
  green,
  grey,
  imgInvalid,
  imgValid,
  red,
  validationType,
} from "../MyConsts";
import { MyDropdown } from "../MyDropdown";
import { FormSelectArea, MyFormSelect } from "../MyFormSelect";
import { DateSelectorDropdown } from "../MyOwnCalendar";
import { editMode } from "../MySelectors";
import {
  calcColor,
  calcInvalidation,
  calcValidation,
  formatInput,
} from "../util/utilities";

const Wrapper = styled.div(
  ({ editable, isValid, isInvalid, colorize, image, type }) => `
  height: 100%;
  font-size: 0.875rem;
  color: black !important;
  display: flex;
  flex: nowrap;
  justify-content: center;
  box-shadow: none;
  transition: all 0.2s ease;
  border: ${editable ? `1.5px solid ${colorize()}` : `none`};

  .dummy {
    .text-span{
    overflow: hidden;
    text-overflow: ellipsis;
    }
    display: flex;
    justify-content: center;
    text-align: center;
    white-space: nowrap;
    background-repeat: no-repeat;
    background-size: calc(0.75em + 1.55rem) calc(0.55em + 0.8rem);
    background-position: left;
    background-image: ${image};
    padding-right: ${
      type === "constant" && editable
        ? "1.8rem !important"
        : !editable & (isValid || isInvalid)
        ? "1rem !important"
        : "4px"
    };
    padding-left: ${
      !editable && (isValid || isInvalid) ? "2rem !important;" : "4px;"
    }
    ${
      editable
        ? `min-height:0;
          //visibility: hidden;
          //height: 0;
          `
        : `min-height: 25px;
          visibility: visible;
          height: auto;
          padding-top: 2px;
          padding-bottom: 2px;
          `
    }
  }
  :focus-within {
    box-shadow: 0 0 0 0.2rem ${colorize("50%")};
    border: 1.5px solid ${colorize()};
    background-color: white;
  }
  :hover {
    background-color: white;
    .text-span {
      ${!editable && "overflow: auto; text-overflow: inherit;"}
    }
  }
  select {
    height: 25px;
    color: black;
    border: none;
    width: 100%;
    background-color: transparent;
    :focus {
      border: none;
      box-shadow: none;
      outline: none;
    }
    :focus-visible {
      outline: none;
    }
    padding-right: 0rem !important;
    padding-left: 0rem !important;
  }
  input,
  textarea {
    width: 100%;
    background-color: transparent;
    border: none;
    text-align: center;
    resize: none;
    vertical-align: bottom;

    padding-right: 0.25rem !important;
    padding-left: 0.25rem !important;

    overflow: hidden;
    :focus {
      outline: none;
    }
  }
`
);

const Input2 = React.memo((props) => {
  const { value, type, style } = props.data;

  const isValid = calcValidation(value + "", validationType(type), false);
  const isInvalid = calcInvalidation(value + "", validationType(type), true);
  // const edi = useSelector(editMode);
  // const editable = props.editable && edi;
  // console.log(props);
  return (
    <MyNewInput
      {...{
        ...props,
        isValid,
        isInvalid,
        value,
        type,
      }}
    />
  );
});
export const MyNewInput = React.memo((props) => {
  const {
    value = "",
    isValid = false,
    isInvalid = false,
    editable = true,
    data,
    type,
  } = props;
  const { measurement, maxWidth = "150px", minWidth = "10px" } = props.data;
  const color = (percent) =>
    calcColor(isInvalid ? red : isValid ? green : grey, percent);
  // console.log(props);
  const img = isInvalid ? imgInvalid : isValid ? imgValid : "none";
  // const edi = useSelector(editMode);
  // const edit = editable && edi;
  return (
    <>
      <Wrapper
        colorize={color}
        editable={editable}
        measurement={measurement}
        isValid={isValid}
        isInvalid={isInvalid}
        image={img}
        type={type}
        style={{
          maxWidth,
          minWidth,
        }}
      >
        <div
          className="justify-content-center d-flex "
          style={{
            width: "fit-content",
            overflow: "auto",
            flexDirection: "column",
          }}
        >
          <div className="dummy">
            {/* <span className="text-span">{formatInput(value, type)}</span> */}

            <span className="text-span">{value}</span>
            {!editable && <span className="ps-1">{measurement}</span>}
          </div>
          {/* {editable && <Compi {...props} />} */}
        </div>
      </Wrapper>
    </>
  );
});

const LazyComp = React.lazy(() => import("../LazyComponent"));
const Compi = (props) => {
  return (
    <div>
      <Suspense fallback={<div>{props.value}</div>}>
        <LazyComp {...props}></LazyComp>
      </Suspense>
    </div>
  );
};

export default Input2;
